$input-color:                           $purple !default;
$input-border-color:                    $dodger-blue !default;
$input-border-width:                    1px !default;
$input-box-shadow:                      inset 0 0 0 2px $gray-200 !default;

$input-border-radius:                   $border-radius !default;
